import "./App.css";
import Post from "./Components/Posts";
import { useState, useEffect } from "react";
import { db, auth } from "./firebase";
import { makeStyles } from "@material-ui/core/styles";
import { Button } from "@material-ui/core";
import Modal from "@material-ui/core/Modal";
// import Instagram from './Components/Instagram'

function getModalStyle() {
 const top = 50;
 const left = 50;

 return {
  top: `${top}%`,
  left: `${left}%`,
  transform: `translate(-${top}%, -${left}%)`,
 };
}

const useStyles = makeStyles((theme) => ({
 paper: {
  position: "absolute",
  width: 400,
  backgroundColor: theme.palette.background.paper,
  border: "2px solid #000",
  boxShadow: theme.shadows[5],
  padding: theme.spacing[(2, 4, 3)],
 },
}));

function App() {
 const classes = useStyles();
 const [modalStyle] = useState(getModalStyle);
 const [posts, setPosts] = useState([]);
 const [open, setOpen] = useState(false);
 const [username, setUsername] = useState("");
 const [password, setPassword] = useState("");
 const [email, setEmail] = useState("");
 const [user, setUser] = useState(null);
 const [openSignIn, setOpenSignIn] = useState("");

 useEffect(() => {
  const unsubscribe = auth.onAuthStateChanged((authUser) => {
   if (authUser) {
    //user has logged in....
    console.log(authUser);
    setUser(authUser);
   } else {
    //user has logged out...
    setUser(null);
   }
  });
  return () => {
   //perform some cleanup actions...
   unsubscribe();
  };
 }, [user, username]);

 useEffect(() => {
  db.collection("posts").onSnapshot((snapshot) => {
   setPosts(
    snapshot.docs.map((doc) => ({
     id: doc.id,
     post: doc.data(),
    }))
   );
  });
 }, []);

 const under = {
  position: "absolute",
  right: "5 %",
 };

 const signUp = (event) => {
  event.preventDefault();
  auth
   .createUserWithEmailAndPassword(email, password)
   .then((authUser) => {
    return authUser;
   })
   .catch((error) => alert(error.message));
  setOpen(false);
 };

 const signIn = (event) => {
  event.preventDefault();

  auth
   .signInWithEmailAndPassword(email, password)
   .catch((error) => alert(error.message));
  setOpenSignIn(false);
 };

 return (
  <div className="App">
   <h3 style={under}>Under construction...</h3>
   <Modal open={open} onClose={() => setOpen(false)}>

     <div style={modalStyle} className={classes.paper}>


      <form className="app_signup">
       <center>
        <img
         className="app_headerImage"
         src="https://fontmeme.com/images/instagram-new-logo.png"
         alt=""
        />
       </center>

       <input
        placeholder="email"
        type="email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
       />

       <input
        placeholder="password"
        type="password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
       />

       <Button onClick={signIn}>Signup</Button>
      </form>
    
    </div>
   </Modal>

   <Modal open={openSignIn} onClose={() => setOpenSignIn(false)}>
    <div>
     <div style={modalStyle} className={classes.paper}></div>

     <div className="app__header">
      <form className="app_signup">
       <center>
        <img
         className="app_headerImage"
         src="https://fontmeme.com/images/instagram-new-logo.png"
         alt=""
        />
       </center>

       <input
        placeholder="username"
        type="username"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
       />

       <input
        placeholder="email"
        type="email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
       />

       <input
        placeholder="password"
        type="password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
       />

       <Button onClick={signIn}>Signup</Button>
      </form>
     </div>
    </div>
   </Modal>

   <div className="app__header">
    <img
     className="app_headerImage"
     src="https://fontmeme.com/images/instagram-new-logo.png"
     alt=""
    />
   </div>

   {user ? (
    <Button onClick={() => auth.signOut}>Logout</Button>
   ) : (
    <div className="login_container">
     <Button onClick={() => setOpen(true)}>SignIn</Button>
     <Button onClick={() => setOpen(true)}>Signup</Button>
    </div>
   )}

   <h1>HEllo Hash Developer lets build Instagram clone</h1>

   {posts.map(({ id, post }) => (
    <Post
     key={id}
     username={post.username}
     caption={post.caption}
     imageUrl={post.imageUrl}
    />
   ))}
  </div>
 );
}

export default App;
